.CustomerOverview {
	width: 100%;
	padding: 40px;
	box-sizing: border-box;
	&-title {
		font-style: normal;
		font-weight: 600;
		font-size: 40px;
		line-height: 48px;
		letter-spacing: -0.02em;
		color: #272b30;
		margin-bottom: 25px;
		margin-top: 15px;
	}

	&-inner {
		.CustomerOverview-left {
			width: 60%;
		}

		.CustomerOverview-right {
			width: 40%;
		}
	}
}
